import React, {useState} from 'react';

function Post(props) {
    const {date, content, hares, tags, img} = props.post;
    const [likes, setLikes] = useState(props.post.likes);
    const [shares, setShares] = useState(props.post.shares);
    const [isLikePressed, setIsLikePressed] = useState(false);
    function handleClicks(){
        if (!isLikePressed){
            setLikes(likes + 1);
            setIsLikePressed(true);
        }else{
            setLikes(likes - 1);
            setIsLikePressed(false);
        }
    }
// How to display tags
//How to check if Tags are available check length of array  tags.length>0

    return (
        <div className="panel panel-default">
            <div className="panel-body">
            <p>Date: {date}</p>
            </div>
            <div className="panel-body">
                {content.map((paragraph,index)=>{
                    return <p key={"p-"+index} >{paragraph}</p>
                })}
            </div>
            <div className="panel-body">
                <img src={img} style={{width: "100%"}} />
            </div>
            <div className="panel-footer">
            <button className="btn btn-default" onClick={handleClicks}> {isLikePressed ? "Unlike" : "Like"}  {likes}</button>
            <button className="pull-right btn btn-secondary">Share {shares}</button>
            </div>
        </div>
    );
}

export default Post;